<template lang="pug">
  .app
    v-btn(v-if="showAddButton" fab fixed bottom right color="red" @click="onNewTopic")
      v-icon add

    v-app
      v-toolbar(app="")
        v-icon all_inbox
        v-icon memory
        v-toolbar-title
          <router-link class="md-title" :to="{ name: 'home', params: {}}">ExoCortex</router-link>

      v-content(app="")
        router-view
</template>

<script>
import { VApp, VToolbar, VContent, VContainer, VBtn } from 'vuetify/lib'

export default {
  name: 'app',
  components: {
    VApp,
    VToolbar,
    VContent,
    VContainer,
    VBtn,
  },
  data() {
    return {
      showAddButton: false,
    }
  },
  mounted () {
    this.$store.dispatch('initialize')
    this.showAddButton = this.$router.currentRoute.path === "/"
    this.$router.afterEach((to, from) => {
      this.showAddButton = this.$router.currentRoute.path === "/"
    })
  },
  methods: {
    onNewTopic () {
      this.$store.dispatch('selectTopic', undefined)
      this.$router.push('/edit')
    }
  }
}
</script>

